.navbar {
  margin: 0px;
  padding: 0px;
  width: 100%;
  z-index: 1;
  position: fixed;
  top: 0px;
  font-family: 'Kaushan Script', cursive;
  box-shadow: 0px 10px 15px rgba(33, 33, 33, 0.66);
  background-color: rgb(255, 255, 255);

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  text-decoration: none;
  color: #000;
}

.navbar .title {
  border: 1px solid red;
  margin: 0;

  width: 350px;
  font-size: 40px;
  text-align: left;
  font-weight: 500;
}

.navbar .navlist {
  margin: 0px;
  padding: 0px;
  width: 100%;
  background: rgb(255, 255, 255);
}

.navlist .home {
  display: none;
}

.navbar .button-container {
  display: none;
}

.navlist ul {
  list-style-type: none;
  margin: 0;
  padding: 0;

  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.navlist a {
  padding: 8px 12px;
  font-weight: 500;
  font-size: 30px;
  color: rgb(166, 166, 166);
  -webkit-transition: color 0.5s ease-in-out;
     -moz-transition: color 0.5s ease-in-out;
       -o-transition: color 0.5s ease-in-out;
      -ms-transition: color 0.5s ease-in-out;
          transition: color 0.5s ease-in-out;
}

.navlist ul li a.active {
  color: #000;
}

.navlist ul li a:hover:not(.active) {
  color: #444;
}

@media (max-width: 1300px) {
  .navbar .title {
    width: 280px;
  }

  .navlist {
    width: 100%;
    position: absolute;
    top: 59px;
    box-shadow: 0px 10px 15px rgba(33, 33, 33, 0.66);
    overflow: hidden;

    height: 0;
    transition: height 0.5s;
  }

  .navlist .home {
    display: block;
  }

  .navlist.open {
    height: auto;
    height: 225px;
  }

  .navlist ul {
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: stretch;
  }

  .navlist ul li a {
    padding: 0;
    display: block;
  }

  .navbar .button-container {
    background-color: rgb(244, 244, 244);
    border-radius: 5px;
    margin: 0;
    padding: 4px 8px;
    margin-right: 8px;
    font-size: 25px;
    margin-left: 0px;
    transition: background-color 0.5s;
    z-index: 10;
    width: 35px;
    cursor: pointer;
    display: block;
    color: #000;
  }

  .button-container:hover {
    background-color: rgb(230, 230, 230);
  }

  .button-container.open {
    background-color: rgb(220, 220, 220);
  }

  .bar1.open {
    transform: translateY(0.5px) translateX(9px) rotate(-135deg) scaleX(0.8);
    background-color: rgb(255, 255, 255);
  }

  .bar2.open {
    transform: translateY(2px) translateX(0px) rotate(90deg) scaleY(1.2);
    background-color: rgb(255, 255, 255);
  }

  .bar3.open {
    transform: translateY(-20.5px) translateX(-9px) rotate(135deg) scaleX(0.8);
    background-color: rgb(255, 255, 255);
  }

  .button-container .button {
    display: inline-block;
    cursor: pointer;
  }

  .bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #333;
    margin: 6px 0;
    transition: 0.4s;
  }
}

@media (max-width: 768px) {
  .navbar .title {
    width: 220px;
    font-size: 32px;
  }
}
